<template>
  <div class="exchange-coin-info">
    <div class="exchange-coin-info__body">
      <Text theme="primary" size="14px" weight="500" line-height="17px" mb="6px">
        {{ label }}
      </Text>
      <Text
        variant="p"
        :size="isMobile ? '28px' : '32px'"
        :line-height="isMobile ? '32px' : '42px'"
        weight="bold"
        :mb="isMobile ? (hideRate ? '0' : '8px') : '0'"
        :mt="isMobile ? (hideRate ? '11px' : '8px') : '0'"
        color="black"
      >
        {{ sum }}
      </Text>
      <div v-if="!hideRate" class="exchange-coin-info__body-rate">
        1 {{ coinLabel }} = ${{ rate }}
      </div>
    </div>
    <div class="exchange-coin-info__icon">
      <Icon
        :name="coin.icon + '-circle'"
        height="60px"
        width="60px"
      />
    </div>
  </div>
</template>

<script>
import Icon from '@/shared/components/Icon/Icon'
import { computed } from 'vue'
import Text from '@/shared/components/UIKit/Text/Text'
import { useBreakpointsMedia } from '@/shared/plugins/breakpoints'
export default {
  name: 'ExchangeCoinInfo',
  components: { Text, Icon },
  props: {
    coin: {
      type: Object,
      default: () => {}
    },
    rate: {
      type: Number,
      default: 0
    },
    hideRate: {
      type: Boolean,
      default: false
    },
    sum: {
      type: Number,
      default: 0
    },
    label: {
      type: String,
      default: ''
    }
  },
  setup (props) {
    const { isMobile } = useBreakpointsMedia()
    const coinLabel = computed(() => props.coin?.key?.toUpperCase())

    return {
      isMobile,
      coinLabel
    }
  }
}
</script>

<style lang="scss">
.exchange-coin-info {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__body {

    &-label {
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;

      color: #D48343;
    }
    &-rate {
      font-size: 12px;
      line-height: 14px;

      color: #000000;
      opacity: 0.3;
    }
  }
}
</style>
